<template>
  <div class="fill-height">
    <div id="chat-history">
      <div class="chat-element" v-for="data in getMessages" :key="data.id">
        <v-layout column>
          <div class="chat-text body-1">{{data.user}}</div>
          <div class="chat-text subheading">{{data.message}}</div>
        </v-layout>
      </div>
    </div>
    <div id="chat-bar">
      <v-layout row align-center>
        <v-flex xs10>
          <v-text-field
          label="Message"
          auto-grow
          autofocos
          rows="2"
          v-model="message"
        ></v-text-field>
        </v-flex>
        <v-flex xs2>
          <v-btn color="primary" @click="send()">Send</v-btn>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Top',
  created: function () {
    try {
      var ws = new WebSocket('/ws')

      ws.onclose = function () {
        this.$router.push('/')
      }

      ws.onmessage = function (event) {
        var msg = JSON.parse(event.data)

        this.messages.push(msg)

        console.log(msg)
      }

      this.ws = ws
    } catch (e) {
      alert(e)
      this.$router.push('/')
    }
  },
  methods: {
    send: function () {
      var msg = this.message
      this.message = ''

      this.ws.send({
        user: this.$store.state.name,
        message: msg
      })
    }
  },
  computed: {
    getMessages: function () {
      var idx = 0
      for (var i of this.messages) {
        i.id = idx
        idx++
      }

      return this.messages
    }
  },
  data () {
    return {
      messages: [],
      message: '',
      ws: null
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chat-text {
  text-align: left;
}
.chat-element {
  margin-top: 2px;
  margin-bottom: 2px;
  background-color:rgb(233, 233, 233);
}

#chat-history {
  overflow: scroll;
  height: 100%;
  padding-bottom: 70px;
}
#chat-bar {
  width: 100%;
  position: absolute;
  bottom: 0;
  background: white;
  height: 70px;
}

#all {
  height: 100%;
}
</style>



